<template>
	<view class="">
		<view class="infoDetails">
			<swiper class="infoImgs" :indicator-dots="true" :circular="true" :interval="3000" :duration="1000">
				<swiper-item>
					<view class="swiper-item">
						<image class="pic" src="../../static/banner1.jpg" mode="aspectFill"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item">
						<image class="pic" src="../../static/banner2.jpg" mode="aspectFill"></image>
					</view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item">
						<image class="pic" src="../../static/banner3.jpg" mode="aspectFill"></image>
					</view>
				</swiper-item>
			</swiper>
			
			<view class="detailsContent">
				<view class="userInfo">
					<view class="userImg">
						<image class="pic" src="../../static/user-head.png" mode=""></image>
					</view>
					<view class="userName">
						物流大佬
					</view>
				</view>
				<view class="introduce">
					我们公司先提供各种物流承接服务，行业的标杆，最 快、最强、最棒，一定及时的将您所运送的物品及时 送到指定位置。
				</view>
				<view class="baseInfo">
					<view class="baseInfoIcon">
						<image src="../../static/icon_timer.png" mode=""></image>
						<text>2021-10-29 15:37</text>
					</view>
					<view class="baseInfoIcon">
						<image src="../../static/icon_addr-line.png" mode=""></image>
						<text>河南省郑州市管城回族区升龙广场</text>
					</view>
				</view>
			</view>
		</view>

		<!-- 底部操作 -->
		<view class="bottomOperation baseflex" :style="isIphoneX ? 'height: 166rpx;' : 'height: 98rpx;'">
			<view class="operationIcon">
				<view class="operationIconItem">
					<image src="../../static/icon_share-line.png" mode=""></image>
					<view class="txt">
						分享
					</view>
				</view>
				<view class="operationIconItem">
					<image src="../../static/icon_follow-goods.png" mode=""></image>
					<view class="txt">
						收藏
					</view>
				</view>
			</view>
			<view class="operationBtn" @click="callTel">
				一键联系
			</view>
		</view>
	</view>
</template>

<script>
	let app = getApp()
	export default {
		data(){
			return {
				isIphoneX: false, // iphoneX手机
				callNum: '800-820-8820',
			}
		},
		onLoad() {
			this.isIphoneX = app.globalData.isIphoneX;
		},
		methods:{
			callTel(){
				uni.makePhoneCall({
				  phoneNumber: this.callNum,
				  success(res) {
				    console.log(res, '拨打电话');
				  },
				  fail(err) {
				    console.log(err);
				  }
				})
			},
		}
	}
</script>

<style lang="less">
	.infoDetails {
		padding: 20rpx 30rpx;
		.infoImgs {
			width: 690rpx;
			height: 690rpx;
			overflow: hidden;
			border-radius: 20rpx;
			swiper-item{
				width: 100%;
				height: 100%;
				.swiper-item{
					width: 100%;
					height: 100%;
				}
			}
		}
		.detailsContent{
			padding: 40rpx 0;
			.userInfo{
				display: flex;
				align-items: center;
				.userImg{
					width: 60rpx;
					height: 60rpx;
					margin-right: 20rpx;
					border-radius: 50%;
					overflow: hidden;
				}
				.userName{
					font-size: 36rpx;
					color: #333;
				}
			}
			.introduce{
				font-size: 28rpx;
				color: #333;
				line-height: 32rpx;
				margin: 20rpx 0 40rpx;
			}
			.baseInfoIcon{
				display: flex;
				margin-bottom: 20rpx;
				image{
					width: 24rpx;
					height: 24rpx;
					margin-right: 10rpx;
					padding-top: 6rpx;
				}
				text{
					font-size: 24rpx;
					color: #999;
				}
			}
		}
	}

	.bottomOperation {
		width: 750rpx;
		height: 98rpx;
		background: #ffffff;
		box-shadow: 0rpx 0rpx 8rpx 2rpx rgba(0, 0, 0, 0.25);
		padding: 6rpx 30rpx;
		position: fixed;
		left: 0;
		bottom: 0;
		align-items: flex-start;

		.operationIcon {
			display: flex;
			align-items: center;

			.operationIconItem {
				margin-right: 64rpx;
				padding: 6rpx 0;

				image {
					width: 48rpx;
					height: 48rpx;
				}

				.txt {
					font-size: 24rpx;
					color: #333;
				}
			}
		}

		.operationBtn {
			width: 172rpx;
			height: 64rpx;
			line-height: 64rpx;
			text-align: center;
			font-size: 28rpx;
			color: #fff;
			background: #ff2d2d;
			margin-top: 14rpx;
		}
	}
</style>
